import { Component, OnInit } from "@angular/core";
import { User } from "@app/shared/models/user.model";
import { AuthService } from "@app/core/service/auth.service";
import { TimesheetsServices } from "./timesheets.services";

@Component({
  selector: "app-timesheets",
  templateUrl: "./timesheets.component.html",
  styleUrls: ["./timesheets.component.css"],
})
export class TimesheetsComponent implements OnInit {
  cols: any = [];
  state: any;
  userId = null;
  user: User;
  loading: boolean = false;
  timesheetsHistoryList: any;
  employeeDetailsId: any;
  timesheetsType: string;
  recordData: any;

  constructor(
    private authService: AuthService,
    private timesheetsService: TimesheetsServices
  ) {}

  ngOnInit(): void {
    this.cols = [
      { field: "status", header: "Status" },
      { field: "startDate", header: "Start Date" },
      { field: "endDate", header: "End Date" },
    ];
    this.employeeDetailsId = this.authService.currentUser.employeeDetailsId;

    this.getTimesheetsHistory(this.employeeDetailsId);
  }
  getTimesheetsHistory(empId) {
    this.timesheetsService.getTimesheetsHistory(empId).subscribe((res) => {
      if (res.statusCode == 200) {
        this.timesheetsHistoryList = res.responsePayload;
      } else {
        this.timesheetsHistoryList = [];
      }
    });
  }
  showDialogToAdd() {
    this.timesheetsType = "Add";
  }
  ontimesheetsAddCloseClick(event) {
    this.getTimesheetsHistory(this.employeeDetailsId);
    this.timesheetsType = "";
  }
  showDialogToEdit(rowData) {
    this.timesheetsType = "Edit";
    this.recordData = rowData;
  }
  getStatus(data) {
    switch (data) {
      case "1":
        return "Saved";
      case "2":
        return "Submitted";
      case "3":
        return "Approved";
      case "4":
        return "Rejected";
    }
  }
}
